<template>
  <div class="team_details tile gray-tile">
    <!-- WHILE LOADING -->
    <div v-if="loading" class="text-center">
      <div class="container py-3 text-left">
        <a class="btn btn-primary btn-sm back" @click="onBack"><i class="fa fa-arrow-left fa-fw"></i> Back</a>
      </div>
      <div class="text-center">
        <h2 class="py-3">...</h2>
      </div>
      <p class="lead">Baking bread...</p>
      <img src="../../../assets/loading.svg" alt="">
    </div>
    <!-- CONTENT -->
    <div v-else>
      <div class="container py-3 text-left">
        <div class="row">
          <div class="col-4 text-left">
            <img class="img-thumbnails" src="data:image/jpeg;base64," alt="Avatar">
            <ul>
              <li class="tiled inner-tile">
                <span class="title"><i class="fa fa-trophy fa-fw"></i> MMR:</span>
                <ul>
                  <li>Estimate: ----</li>
                  <li>Solo: ----</li>
                  <li>Party: ----</li>
                </ul>
              </li>
              <li class="tiled inner-tile">
                <span class="title"><i class="fa fa-map-marker fa-fw"></i> Regions:</span>
                <ul v-for="(region, index) in team.regions" :key="index">
                  <li>{{region}}</li>
                </ul>
              </li>
              <li class="tiled inner-tile">
                <span class="title"><i class="fa fa-globe fa-fw"></i> Languages:</span>
                <ul v-for="(language, index) in team.languages" :key="index">
                  <li>{{language}}</li>
                </ul>
              </li>
              <li class="tiled inner-tile">
                <span class="title"><i class="fa fa-headphones fa-fw"></i> Communication Method:</span>
                <ul v-for="(comm, index) in team.comms" :key="index">
                  <li>{{comm}}</li>
                </ul>
              </li>
              <li class="tiled inner-tile">
                <span class="title"><i class="fa fa-pie-chart fa-fw"></i> Recruiting:</span>
                <ul v-for="(recruit, index) in team.recruiting" :key="index">
                  <li>{{recruit}}</li>
                </ul>
              </li>
            </ul>
          </div>
          <div class="col-8 text-left">
            <header class="d-flex justify-content-between align-items-center">
              <h2>{{team.teamName}}</h2>
              <!--<ul class="links">-->
              <!--  <li v-if="inReceived"><a class="scale-up" @click="acceptRequest(player.steamId)"><i class="fa fa-check fa-fw"></i></a></li>-->
              <!--  <li v-if="inReceived"><a class="scale-up" @click="declineRequest(player.steamId)"><i class="fa fa-times fa-fw"></i></a></li>-->
              <!--  <li v-else-if="inSent"><a class="scale-up" @click="cancelRequest(player.steamId)"><i class="fa fa-ban fa-fw"></i></a></li>-->
              <!--  <li v-else-if="inAccepted"><a class="scale-up" @click="deleteFriend(player.steamId)"><i class="fa fa-trash fa-fw"></i></a></li>-->
              <!--  <li v-else><a class="scale-up" @click="sendRequest(player.steamId)"><i class="fa fa-user-plus fa-fw"></i></a></li>-->
              <!--  <li><a :href="`http://www.steamcommunity.com/profiles/${player.steamId}`" class="scale-up" target="_blank"><i class="fa fa-steam-square fa-fw"></i></a></li>-->
              <!--  <li><a class="scale-up" @click="updatePlayer"><i class="fa fa-refresh fa-fw"></i></a></li>-->
              <!--</ul>-->
              <!--Icon only changes after refreshing NEED TO FIX-->
              <router-link v-if="isTeamAdmin" :to="{ name: 'teamEdit', params: { id: team._id }}" class="btn scale-up"><i class="fa fa-edit"></i></router-link>
              <a v-if="isTeamMember || isTeamAdmin"  class="btn btn-danger btn-sm float-right" @click="leaveTeam"><i class="fa fa-times fa-fw"></i></a>
              <a v-else-if="isPending"  class="btn btn-warning btn-sm float-right" @click="cancelTeamRequest"><i class="fa fa-ban fa-fw"></i></a>
              <a v-else class="btn btn-primary btn-sm float-right" @click="sendTeamRequest()"><i class="fa fa-plus fa-fw"></i></a>
            </header>
            <div class="tiled description inner-tile">
              <p>
                <span class="title">Description:</span><br>
                {{team.description}}
              </p>
            </div>
            <!--<div class="tiled heroes inner-tile">-->
              <!--<p> -->
              <!--  <span class="title">Most Played Heroes</span>-->
              <!--  <ul id="example-1">-->
              <!--    <li v-for="(hero, index) in player.heroes.slice(0,5)" :key="index">-->
              <!--      {{ hero }}-->
              <!--    </li>-->
              <!--  </ul>-->
              <!--</p>-->
            <!--</div>-->
            <div class="tiled teams inner-tile">
              <p class="title">Teams</p>
            </div>
            <div class="tiled playstyle inner-tile">
              <p class="title">Playstyle</p>
            </div>
            <div class="tiled other inner-tile">
              <p class="title">Other</p>
            </div>
            <div class="tiled other inner-tile">
              <p class="title">Team Members</p>
              <ul class="list-group clearfix" v-for="(player, index) in team.teamAdmins" :key="index">
                <li class="list-group-item inner-tile">
                  <app-player-item :player="player"></app-player-item>
                </li>
              </ul>
              <ul class="list-group clearfix" v-for="(player, index) in team.teamMembers" :key="index">
                <li class="list-group-item inner-tile">
                  <app-player-item :player="player"></app-player-item>
                </li>
              </ul>
            </div>
            <div v-if="isTeamAdmin" class="tiled other inner-tile">
              <p class="title">Pending Requests</p>
              <span v-if="team.pending.length !== 0">
                <ul class="list-group clearfix" v-for="(player, index) in team.pending" :key="index">
                  <li class="list-group-item inner-tile">
                    <div class="row">
                      <div class="col-sm-3">
                        <img id="avatar" :src="player.img" alt="Avatar">
                      </div>
                      <div class="col-sm-9">
                        <router-link :to="{ name: 'playerDetails', params: { id: player.steamId }}"><h5>{{ player.steamName }}</h5></router-link>
                        <a class="btn btn-danger btn-sm float-right" @click="declineTeamRequest(player)"><i class="fa fa-times fa-fw"></i></a>
                        <a class="btn btn-success btn-sm float-right" @click="acceptTeamRequest(player)"><i class="fa fa-check fa-fw"></i></a>
                      </div>
                    </div>
                  </li>
                </ul>
              </span>
              <p v-else class="title">You have no pending requests</p>
            </div>
          </div>
        </div>
      </div>

    </div>
    <br><br>
  </div>
</template>

<script>
import PlayerItem from '../players/Player_item.vue'
import PlayerDetails from '../players/Player_details.vue'

export default {
  computed: {
    user () {
      if (!this.$store.getters.user) return false
      return this.$store.getters.user
    },
    team () {
      return this.$store.getters.team
    },
    loading () {
      return this.$store.getters.team_loading
    },
    isTeamAdmin () {
      if (this.$store.getters.user) return this.$store.getters.team.teamAdmins.filter(admin => (admin.steamId === this.$store.getters.user.steamId)).length
      else return false
    },
    isTeamMember () {
      if (this.$store.getters.user) return this.$store.getters.team.teamMembers.filter(member => (member.steamId === this.$store.getters.user.steamId)).length
      else return false
    },
    isPending () {
      if (this.$store.getters.user) return this.$store.getters.team.pending.filter(pending => (pending.steamId === this.$store.getters.user.steamId)).length
      else return false
    }
  },
  methods: {
    getTeam () {
      this.$store.dispatch('getTeam', this.$route.params.id)
    },
    sendTeamRequest () {
      this.$store.dispatch('sendTeamRequest', this.$route.params.id)
    },
    declineTeamRequest (player) {
      this.$store.dispatch('declineTeamRequest', player)
    },
    acceptTeamRequest (player) {
      this.$store.dispatch('acceptTeamRequest', player)
    },
    cancelTeamRequest () {
      confirm('Are you sure?')
      this.$store.dispatch('cancelTeamRequest', this.$route.params.id)
    },
    leaveTeam () {
      confirm('Are you sure?')
      this.$store.dispatch('leaveTeam', this.$route.params.id)
    },
    onBack () {
      this.$router.go(-1)
    }
    // updateTeam () {
    //   console.log('Updating Players...')
    //   this.$store.dispatch('updateTeam', this.$route.params.id)
    // }
  },
  components: {
    appPlayerItem: PlayerItem,
    appPlayerDetails: PlayerDetails
  },
  // watch: {
  //   '$route.params.id' (newId, oldId) {
  //     this.$store.dispatch('resetTeamDetails')
  //     this.getTeam(newId)
  //   }
  // },
  activated () {
    this.$store.dispatch('resetTeamDetails')
    console.log('Fetching team profile...')
    this.getTeam()
  }
}
</script>

<style scoped>
  .tiled {
    padding: 5px 10px;
    border-radius: 5px;
    margin-bottom: 15px;
  }

  a {
    color: white;
    cursor: pointer;
  }

  header {
    border-bottom: 3px solid #666;
    margin-bottom: 15px;
  }

  .scale-up i{
    font-size: 1.8em;
    cursor: pointer;
    transform: scale(1);
    transition: 0.3s;
  }

  .scale-up:hover i{
    transform: scale(1.3);
    transition: 0.1s;
  }

  .scale-up:active i{
    transform: scale(1.1); 
  }

  .scale-up:focus i{
    transform: scale(1.1); 
  }
  
  a.btn.back {
    text-align: left;
    margin-bottom: 15px;
  }

  .img-thumbnails {
    border: 3px solid #fff;
    border-radius: 5px;
    margin-bottom: 30px;
    width: 100%;
  }

  ul {
    padding-left: 0;
  }

  li {
    list-style-type: none;
    line-height: 1.6em;
    padding-left: 15px;
  }

  li.tiled {
    margin-bottom: 10px;
  }

  ul.links {
    margin-bottom: 5px;
  }
  ul.links li {
    display: inline-block;
  }

  .title {
    font-weight: 600;
    /* color: #106CD6; */
    color: orange;
  }
  
  #avatar {
    width: 40%;
    border: 3px solid #fff;
    border-radius: 5px;
  }
  
  a.btn {
    margin-left: 8px;
  }
  
  a.btn i {
    font-size: 1em;
  }
  
  h5 {
    display: inline-block;
    margin-bottom: 15px;
  }
  
  a:hover h5{
    text-decoration: underline;
  }
</style>


